___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Segoe UI', sans-serif;

  background-color: #313131;

  margin: 0;
}

.conteudo {
  display: flex;
  flex-direction: column;

  padding: 0 5%;
}

h1 {
  font-size: 3rem;
  font-weight: 700;
  color: #f9f9f9;
}

.descricao {
  font-size: 22px;
  font-weight: 600;
  color: #f9f9f9;
}

.container-formulario {
  display: flex;
  /* justify-content: center; */
  gap: 5px;

  padding: 1%;
}

.pesquisa {
  outline: 1px solid #f9f9f9;

  border:none;
  border-radius: 10px;

  font-size: 18px;

  padding: 5px;
}

.pesquisa-erro {
  outline: 2px solid #DA471B !important;
}

.btn {
  display: grid;
  padding: 15px;

  border: none;
  border-radius: 10px;
}

.btn:hover {
  opacity: 0.9;
  cursor: pointer;
  outline: 1px solid #ffffff;
}

.btn-pesquisar {
  background-color: #4DAD5B;
  color: white;
}

.btn-limpar {
  background-color: #DA471B;
  color: white;
}

.card-pokemon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;

  height: 250px;

  background-color: #e1e1e1;
  color: #313131;

  border-radius: 1rem;

  margin-top: 5%;
}

.notificacao {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  margin-left: auto;
  margin-right: auto;

  text-align: center;
  font-size: 20px;
  font-weight: 600;
  
  background-color: #DA471B;
  border-radius: 10px;

  width: 300px;

  padding: 10px 20px;
}

.notificacao:hover {
  opacity: 0.9;
  cursor: pointer;
}

.grid-pokemons {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 10px;
}`, "",{"version":3,"sources":["webpack://./src/views/pokemon-listagem/pokemon-listagem.css"],"names":[],"mappings":"AAAA;EACE,mCAAmC;;EAEnC,yBAAyB;;EAEzB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,sBAAsB;;EAEtB,aAAa;AACf;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,QAAQ;;EAER,WAAW;AACb;;AAEA;EACE,0BAA0B;;EAE1B,WAAW;EACX,mBAAmB;;EAEnB,eAAe;;EAEf,YAAY;AACd;;AAEA;EACE,qCAAqC;AACvC;;AAEA;EACE,aAAa;EACb,aAAa;;EAEb,YAAY;EACZ,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,eAAe;EACf,0BAA0B;AAC5B;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,SAAS;;EAET,aAAa;;EAEb,yBAAyB;EACzB,cAAc;;EAEd,mBAAmB;;EAEnB,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,OAAO;EACP,QAAQ;;EAER,iBAAiB;EACjB,kBAAkB;;EAElB,kBAAkB;EAClB,eAAe;EACf,gBAAgB;;EAEhB,yBAAyB;EACzB,mBAAmB;;EAEnB,YAAY;;EAEZ,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,2DAA2D;EAC3D,SAAS;AACX","sourcesContent":["body {\r\n  font-family: 'Segoe UI', sans-serif;\r\n\r\n  background-color: #313131;\r\n\r\n  margin: 0;\r\n}\r\n\r\n.conteudo {\r\n  display: flex;\r\n  flex-direction: column;\r\n\r\n  padding: 0 5%;\r\n}\r\n\r\nh1 {\r\n  font-size: 3rem;\r\n  font-weight: 700;\r\n  color: #f9f9f9;\r\n}\r\n\r\n.descricao {\r\n  font-size: 22px;\r\n  font-weight: 600;\r\n  color: #f9f9f9;\r\n}\r\n\r\n.container-formulario {\r\n  display: flex;\r\n  /* justify-content: center; */\r\n  gap: 5px;\r\n\r\n  padding: 1%;\r\n}\r\n\r\n.pesquisa {\r\n  outline: 1px solid #f9f9f9;\r\n\r\n  border:none;\r\n  border-radius: 10px;\r\n\r\n  font-size: 18px;\r\n\r\n  padding: 5px;\r\n}\r\n\r\n.pesquisa-erro {\r\n  outline: 2px solid #DA471B !important;\r\n}\r\n\r\n.btn {\r\n  display: grid;\r\n  padding: 15px;\r\n\r\n  border: none;\r\n  border-radius: 10px;\r\n}\r\n\r\n.btn:hover {\r\n  opacity: 0.9;\r\n  cursor: pointer;\r\n  outline: 1px solid #ffffff;\r\n}\r\n\r\n.btn-pesquisar {\r\n  background-col